<template>
  <div class="login">
    <div class="login__img">
      <img
        src=" https://markdown-1253389072.cos.ap-nanjing.myqcloud.com/202202221429453.png"
        alt=""
        class="login__img__img"
      />
    </div>
    <div class="login__input">
      <input
        placeholder="请输入手机号"
        type="text"
        class="login__input__item login__input__username"
        v-model="phone"
      />
      <input
        placeholder="请输入密码"
        type="password"
        class="login__input__item login__input__password"
        v-model="password"
      />
    </div>
    <div class="login__button">
      <button @click="handleLogin" class="login__button__login">登录</button>
    </div>
    <div class="login__help">
      <a href="javascript:;" @click="goRegister" class="login__help__a"
        >立即注册</a
      >
      <span class="login__help__gap">|</span>
      <a href="javascript:;" class="login__help__a">忘记密码</a>
    </div>
  </div>
  <Toast v-show="isShow" :message="toastMessage" />
</template>

<script>
import { reactive, toRefs } from "vue";
import { useRouter } from "vue-router";
import { post } from "../../utils/request";
import Toast, { showToastEffect } from "../../components/Toast.vue";

const userLoginEffect = (showToast) => {
  const data = reactive({
    phone: "",
    password: "",
  });

  const router = useRouter();

  const handleLogin = async () => {
    try {
      if (data.phone == "") {
        showToast("手机号不能为空!!!");
        return;
      }

      if (data.password == "") {
        showToast("密码不能为空!!!");
        return;
      }

      const result = await post("/user/login", {
        phong: data.phone,
        password: data.password,
      });

      if (result.data.code == "000") {
        localStorage.setItem("isLogin", "true");
        localStorage.setItem("userId", result.data.data.id);

        router.push({ name: "Home" });
      } else {
        showToast("登陆失败,用户名或密码不正确!!!");
      }
    } catch (error) {
      showToast("发送请求失败!!!");
    }
  };

  const { phone, password } = toRefs(data);

  return { phone, password, handleLogin };
};

const userRegisterEffect = () => {
  const router = useRouter();
  const goRegister = () => {
    router.push({ name: "Register" });
  };
  return { goRegister };
};

export default {
  components: { Toast },
  name: "Login",

  setup() {
    const { isShow, toastMessage, showToast } = showToastEffect();
    const { phone, password, handleLogin } = userLoginEffect(showToast);
    const { goRegister } = userRegisterEffect();

    return { handleLogin, goRegister, phone, password, isShow, toastMessage };
  },
};
</script>

<style lang="scss" scoped>
.login {
  text-align: center;

  &__img {
    &__img {
      width: 66rem;
      height: 66rem;
      margin-top: 120rem;
    }
  }

  &__input {
    margin-top: 40rem;
    margin-bottom: 16rem;

    &__item {
      width: 295rem;
      height: 48rem;
      background: #f9f9f9;
      border: 1rem solid rgba(0, 0, 0, 0.1);
      border-radius: 6rem;
      margin-bottom: 16rem;
      font-family: PingFangSC-Regular;
      font-size: 16rem;
      text-indent: 1em;
    }
  }

  &__button {
    margin-bottom: 16rem;

    &__login {
      width: 295rem;
      height: 48rem;
      background: #0091ff;
      box-shadow: 0 4rem 8rem 0 rgba(0, 145, 255, 0.32);
      border-radius: 4rem;
      font-family: PingFangSC-Regular;
      font-size: 16rem;
      color: #ffffff;
    }
  }

  &__help {
    font-family: PingFangSC-Regular;
    font-size: 14rem;
    color: rgba(0, 0, 0, 0.5);
    letter-spacing: 0;

    &__a {
      color: rgba(0, 0, 0, 0.5);
    }

    &__gap {
      margin: 0 12rem;
      font-size: 12rem;
    }
  }
}
</style>
